* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: 0;
  list-style-position: inside;
}

body {
  font-size: 16px;
  font-family: Arial;
  background-image: url("../img/bodyBg.png");
}

body::-webkit-scrollbar {
  width: 12px;
}

body::-webkit-scrollbar-track {
  background-color: #666;
}

body::-webkit-scrollbar-thumb {
  background-color: #ccc;
}

.container {
  max-width: 1300px;
  padding: 10px;
  margin: 0 auto;
}

.swiper-button-white {
  background: #226f93c4;
  background-repeat: no-repeat;
  background-size: 25px 35px;
  background-position: center center;
  min-width: 55px;
  min-height: 55px;
  opacity: 0;
}

.el {
  background-repeat: no-repeat;
  background-size: 25px 35px;
  background-position: center center;
  min-width: 55px;
  min-height: 55px;
}

a {
  color: #1763de;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

.swiper-button-next {
  right: 0px;
}

.swiper-button-prev {
  left: 0px;
}

.tab_wrapper .tabModule_btns {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
  border-radius: 8px;
  overflow: hidden;
  margin-bottom: 25px;
}

.tab_wrapper .tabModule_btns .tabModule_btn {
  flex-basis: calc(100%/4);
  text-align: center;
  padding: 10px;
  text-transform: uppercase;
  background: rgba(240, 114, 35, 0.4);
  color: #000;
  cursor: pointer;
}

.tab_wrapper .tabModule_btns .tabModule_btn.active_tab {
  background: #E8803E;
  color: #fff;
}

.tab_wrapper .tabModule_btns .tabModule_btn.estateTab {
  flex-basis: calc(100%/3);
}

.tab_wrapper .tabModule_bodys .tabModule_body {
  display: none;
}

.tab_wrapper .tabModule_bodys .tabModule_body.active_tab_body {
  display: block;
}

.tab_wrapper .tabModule_bodys .tabModule_body .tabModule_body_item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 15px;
  background: #fff;
  padding: 8px;
  border-radius: 8px;
}

.tab_wrapper .tabModule_bodys .tabModule_body .tabModule_body_item .tabModule_body_item__cover {
  max-width: 250px;
  min-width: 250px;
  max-height: 200px;
  min-height: 200px;
  border-radius: 8px;
  overflow: hidden;
  margin-right: 10px;
}

.tab_wrapper .tabModule_bodys .tabModule_body .tabModule_body_item .tabModule_body_item__cover img {
  display: block;
  width: 100%;
  object-fit: cover;
}

.tab_wrapper .tabModule_bodys .tabModule_body .tabModule_body_item .tabModule_body_item__details {
  flex-basis: 580px;
}

.tab_wrapper .tabModule_bodys .tabModule_body .tabModule_body_item .tabModule_body_item__details h3 {
  font-weight: normal;
}

.tab_wrapper .tabModule_bodys .tabModule_body .tabModule_body_item .tabModule_body_item__details h3 a {
  display: block;
  margin-bottom: 10px;
  text-decoration: none;
  color: #1763DE;
}

.tab_wrapper .tabModule_bodys .tabModule_body .tabModule_body_item .tabModule_body_item__details:hover h3 a {
  text-decoration: underline;
}

.tab_wrapper .tabModule_bodys .tabModule_body .tabModule_body_item .tabModule_body_item__details .location {
  font-size: 14px;
}

.tab_wrapper .tabModule_bodys .tabModule_body .tabModule_body_item .tabModule_body_item__details .text_preview {
  line-height: 25px;
  margin: 15px 0px;
  color: #5d5d5d;
}

.tab_wrapper .tabModule_bodys .tabModule_body .tabModule_body_item .tabModule_body_item__details .room_area {
  display: flex;
}

.tab_wrapper .tabModule_bodys .tabModule_body .tabModule_body_item .tabModule_body_item__details .room_area p {
  margin-right: 50px;
}

.tab_wrapper .tabModule_bodys .tabModule_body .tabModule_body_item .tabModule_body_item__details .room_area p span {
  color: #5d5d5d;
}

.tab_wrapper .tabModule_bodys .tabModule_body .tabModule_body_item .price_date {
  display: flex;
  flex-flow: column;
  height: 146px;
  justify-content: space-between;
}

.tab_wrapper .tabModule_bodys .tabModule_body .tabModule_body_item .price_date .price {
  font-size: 25px;
}

.tab_wrapper .tabModule_bodys .tabModule_body .tabModule_body_item .price_date .date {
  font-size: 14px;
  color: #5d5d5d;
}

.tab_wrapper .tabModule_bodys .tabModule_body .see_more {
  display: block;
  margin-top: 15px;
  text-decoration: none;
  color: #fff;
  background: #49C7EB;
  width: fit-content;
  padding: 10px 20px;
  border-radius: 8px;
  font-size: 15px;
  transition: 0.5s;
}

.tab_wrapper .tabModule_bodys .tabModule_body .see_more:hover {
  background: #29afd4;
}

.lg-backdrop {
  z-index: 9999 !important;
}

.lg-outer {
  z-index: 9999 !important;
}

.wrap {
  width: 100%;
  position: relative;
  height: 0;
  overflow: hidden;
  padding-bottom: 56.25%;
}

.wrap iframe,
.wrap object,
.wrap embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

header[data-homeHeader] {
  background-size: cover;
  min-height: 386px;
  background-position: center center;
}

header[data-homeHeader] .header {
  padding: 30px 10px;
  position: relative;
}

header[data-homeHeader] .header .header_banner_text {
  color: #fff;
}

header[data-homeHeader] .header .header_banner_text h1,
header[data-homeHeader] .header .header_banner_text h2 {
  font-weight: normal;
  text-shadow: 2px 1px 3px #000;
}

header[data-homeHeader] .header .header_banner_text h1 {
  font-size: 24px;
}

header[data-homeHeader] .header .header_banner_text h2 {
  margin-top: 20px;
  font-size: 30px;
}

header[data-homeHeader] .header .header_tab {
  position: absolute;
  top: 200px;
  left: 10px;
  right: 10px;
  border-radius: 8px;
  overflow: hidden;
}

header[data-homeHeader] .header .header_tab .tab_btns {
  display: flex;
  justify-content: space-between;
}

header[data-homeHeader] .header .header_tab .tab_btns .tab_btn {
  flex-basis: calc(100%/4);
  text-align: center;
  padding: 10px;
  text-transform: uppercase;
  background: rgba(49, 85, 108, 0.8);
  color: #fff;
  cursor: pointer;
}

header[data-homeHeader] .header .header_tab .tab_btns .tab_btn.active_tab {
  background: #E8803E;
}

header[data-homeHeader] .header .header_tab .tab_bodys .tab_body {
  display: none;
  background: #fff;
  padding: 15px;
}

header[data-homeHeader] .header .header_tab .tab_bodys .tab_body.active_tab_body {
  display: block;
}

header[data-homeHeader] .header .header_tab .tab_bodys .tab_body .tab_body_sections {
  display: flex;
  justify-content: space-between;
}

header[data-homeHeader] .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item {
  flex-basis: 20%;
  border-right: 2px solid #eee;
}

header[data-homeHeader] .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item:last-child {
  border-right: none;
  flex-basis: 30%;
}

header[data-homeHeader] .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item h3 {
  font-weight: normal;
  margin-bottom: 15px;
  text-transform: capitalize;
}

header[data-homeHeader] .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item label {
  display: block;
  margin-bottom: 8px;
  cursor: pointer;
}

header[data-homeHeader] .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item label input {
  margin-right: 8px;
}

header[data-homeHeader] .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item select,
header[data-homeHeader] .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item input {
  margin-bottom: 8px;
  padding: 5px;
  cursor: pointer;
  background: rgba(23, 99, 222, 0.1);
  border: 1px solid rgba(23, 99, 222, 0.2);
  border-radius: 5px;
}

header[data-homeHeader] .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item select {
  display: block;
  width: 90%;
}

header[data-homeHeader] .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item .tbsi_wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

header[data-homeHeader] .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item .tbsi_wrap .price_filter {
  display: flex;
  justify-content: space-between;
  flex-basis: 100%;
}

header[data-homeHeader] .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item .tbsi_wrap .price_filter .price_filter_inputs {
  display: flex;
}

header[data-homeHeader] .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item .tbsi_wrap .price_filter .price_filter_inputs input {
  width: 48%;
}

header[data-homeHeader] .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item .tbsi_wrap .price_filter select {
  width: 30%;
}

header[data-homeHeader] .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item .tbsi_wrap .sq_filter,
header[data-homeHeader] .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item .tbsi_wrap .floor_filter {
  display: flex;
  align-items: center;
  min-height: 35px;
  flex-basis: 48%;
}

header[data-homeHeader] .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item .tbsi_wrap .sq_filter input,
header[data-homeHeader] .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item .tbsi_wrap .floor_filter input {
  width: 45%;
  font-size: 15px;
  margin-bottom: 0px;
}

header[data-homeHeader] .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item .tbsi_wrap .sq_filter span,
header[data-homeHeader] .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item .tbsi_wrap .floor_filter span {
  font-size: 12px;
  margin-right: 5px;
}

header[data-homeHeader] .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item .tbsi_wrap .code {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 10px;
  width: 100%;
}

header[data-homeHeader] .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item .tbsi_wrap .code input {
  max-width: 150px;
  height: 100%;
  margin-bottom: 0px !important;
}

header[data-homeHeader] .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item .tbsi_wrap .code label {
  font-size: 14px;
  margin-right: 0px;
  margin-bottom: 0px;
}

header[data-homeHeader] .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item .tbsi_wrap .code label input {
  max-width: unset !important;
  width: unset !important;
  margin: unset;
  height: unset;
}

header[data-homeHeader] .header .header_tab .tab_bodys .tab_body button {
  display: block;
  padding: 12px 30px;
  background: #49C7EB;
  border: 1px solid #3eb7d8;
  color: #fff;
  cursor: pointer;
  font-size: 16px;
  margin: 0px 0px 0px auto;
  border-radius: 8px;
  transition: 0.5s;
}

header[data-homeHeader] .header .header_tab .tab_bodys .tab_body button:hover {
  background: #29afd4;
}

header[data-estateHeader] {
  position: relative;
  background-size: cover;
  min-height: 386px;
  background-repeat: no-repeat;
  background-position: center center;
  max-width: 1280px;
  margin: 0 auto;
  overflow: hidden;
}

header[data-estateHeader] h1 {
  position: absolute;
  bottom: 50px;
  left: 30px;
  color: #fff;
  text-shadow: 2px 1px 2px #000;
  font-weight: normal;
}

header[data-propertyDetailsDeader] .container {
  display: flex;
  justify-content: space-between;
  margin-top: 15px;
  margin-bottom: 35px;
  background: #fff;
  border-radius: 5px;
  max-width: 1280px;
}

header[data-propertyDetailsDeader] .container .property_slider {
  width: 600px;
  height: 540px;
  margin-right: 30px;
  overflow: hidden;
}

header[data-propertyDetailsDeader] .container .property_slider #primary-slider .splide__track .splide__list li {
  cursor: pointer;
}

header[data-propertyDetailsDeader] .container .property_slider #primary-slider .splide__track .splide__list li img {
  display: block;
}

header[data-propertyDetailsDeader] .container .property_user_info {
  align-self: center;
  width: 600px;
}

header[data-propertyDetailsDeader] .container .property_user_info #bigPrice {
  font-size: 35px;
  margin-bottom: 35px;
}

header[data-propertyDetailsDeader] .container .property_user_info #agent_contacts {
  color: #5d5d5d;
  margin-bottom: 8px;
}

header[data-propertyDetailsDeader] .container .property_user_info #agent_name {
  padding-bottom: 35px;
  margin-bottom: 15px;
  border-bottom: 1px solid #ccc;
}

header[data-propertyDetailsDeader] .container .property_user_info #agent_tel {
  margin-bottom: 35px;
  padding-bottom: 15px;
  border-bottom: 1px solid #ccc;
}

header[data-propertyDetailsDeader] .container .property_user_info #agent_tel a {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}

header[data-propertyDetailsDeader] .container .property_user_info #agent_tel a img {
  margin-right: 15px;
  width: 25px;
}

header[data-propertyDetailsDeader] .container .property_user_info #agent_tel a span {
  margin-right: 10px;
}

header[data-propertyDetailsDeader] .container .property_user_info #agent_tel a span:nth-child(2) {
  color: #5d5d5d;
}

header[data-propertyDetailsDeader] .container .property_user_info #agent_tel a span:nth-child(3) {
  color: #000;
}

header[data-propertyDetailsDeader] .container .property_user_info #agent_tel a:hover {
  text-decoration: none;
}

header[data-propertyDetailsDeader] .container .property_user_info #agent_tel a:hover span:nth-child(2) {
  color: #5d5d5d;
}

header[data-propertyDetailsDeader] .container .property_user_info #agent_tel a:hover span:nth-child(3) {
  color: #1763de;
}

header[data-propertyDetailsDeader] .container .property_user_info #agent_board_funcs a {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}

header[data-propertyDetailsDeader] .container .property_user_info #agent_board_funcs a img {
  margin-right: 10px;
}

header[data-propertyDetailsDeader] .container .property_user_info #right_date {
  margin-top: 45px;
  color: #5d5d5d;
  text-align: right;
}

