nav .nav {
  display: flex;
  justify-content: space-between;
}

nav .nav .mobileMenuBtn {
  display: none;
}

nav .nav .mobileMenuBtn i {
  font-size: 30px;
  cursor: pointer;
}

nav .nav .mobileCallBlock,
nav .nav .add_new_mob,
nav .nav .mob_langs,
nav .nav .closeMobileMenu {
  display: none;
}

nav .nav .logo {
  flex-basis: 15%;
}

nav .nav .logo a img {
  width: 200px;
}

nav .nav .wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  flex-basis: 80%;
}

nav .nav .wrapper .mobile_logo {
  display: none;
}

nav .nav .wrapper .call_block {
  display: flex;
  justify-content: space-between;
  flex-basis: 100%;
  margin-bottom: 15px;
}

nav .nav .wrapper .call_block .cb_item {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-basis: 32%;
  border-radius: 25px;
  min-height: 45px;
}

nav .nav .wrapper .call_block .cb_item .cb_item__icon {
  margin-right: 25px;
}

nav .nav .wrapper .call_block .cb_item .cb_item__icon img {
  width: 100%;
}

nav .nav .wrapper .call_block .cb_item .cb_item__text {
  text-decoration: none;
  color: #000000;
  font-size: 18px;
}

nav .nav .wrapper .call_block .cb_item:nth-child(1) {
  background: rgba(69, 198, 58, 0.2);
}

nav .nav .wrapper .call_block .cb_item:nth-child(2) {
  background: rgba(115, 96, 242, 0.3);
}

nav .nav .wrapper .call_block .cb_item:nth-child(3) {
  background: rgba(73, 199, 235, 0.3);
}

nav .nav .wrapper #menu {
  display: flex;
}

nav .nav .wrapper #menu li {
  display: inline-block;
  list-style-type: none;
  margin-right: 15px;
  position: relative;
}

nav .nav .wrapper #menu li a {
  color: #000;
  text-decoration: none;
  text-transform: uppercase;
